import React, { useState } from 'react'
import { Card, Btn, statuscolors, Status } from '../Common/Styles/StyledComponents'
import ContentContainer from '../Components/ContentContainer'
import Controls from '../Components/Controls'
import { useGetData, useHeaders } from '../Hooks/getData'
import { API_URI } from '../Endpoint'
import { saveCurrentCandidate, changeStatus } from '../Actions/CandidateActions'
import { useSelector, useDispatch } from 'react-redux'
import axios from 'axios'
import CandidateInfo from '../Components/CandidateInfo'
import { statusColor } from '../Common/Functions/helperFunctions'

import { toast } from '../Components/Toast'
import Loader from '../Components/Loader'

function CandidatePage(props) {
    const headers = useHeaders()
    const {data} = useSelector(state => state.authData)
    const permissions = data && data.authData.role_id.permissions
    const dispatch = useDispatch()
    const [isStatusChanged, setIsStatusChanged] = useState(false)
    const id = props.match.params.id
    const URI = `${API_URI}user/${id}`
    useGetData(URI, saveCurrentCandidate)
    const { candidate } = useSelector(state => state.candidateData)
    console.log(candidate)

    const changeStatusHandler = async (status) => {
        const URL = `${API_URI}status/${candidate && candidate._id}?status=${status}`
        try {
            console.log(headers)
            await axios.patch(URL, {}, {headers})
            setIsStatusChanged(true)
            dispatch(changeStatus(status))
            toast.success('Status Updated', {
                position: toast.POSITION.BOTTOM_RIGHT
            })
        } catch (err) {
            toast.error('Error in status updation', {
                position: toast.POSITION.BOTTOM_RIGHT
            })
        }
    }

    const renderCard = () => {
        if(candidate){
            return <>

            {permissions && permissions.candidate.update ? <Controls title="Status Controls">
                <div>
                    {candidate && candidate.jobs[0].status !== 'Rejected' && <Btn color={statuscolors.failed} onClick={() => changeStatusHandler(3)}>Reject</Btn>}
                    {candidate && candidate.jobs[0].status !== 'Selected' && <Btn color={statuscolors.success} onClick={() => changeStatusHandler(2)}>Select</Btn>}
                    {((candidate && candidate.jobs[0].status !== 'Called For Interview') && (candidate && candidate.jobs[0].status !== 'Selected')) ? <Btn color={statuscolors.review} onClick={() => changeStatusHandler(1)}>Call for interview</Btn> : null}
                </div>
            </Controls> : null}
            <Card>
                <CandidateInfo candidate={candidate} />
            </Card>
            </>
        }
    }

    return (
        <ContentContainer
            title={candidate ? candidate.name:<Loader/>}
            value2={<Status {...statusColor(candidate && candidate.jobs[0].status)}>
                {candidate && candidate.jobs[0].status}
            </Status>}
            justify="flex-start"
        >

            {renderCard()}            
        </ContentContainer>
    )
}

export default CandidatePage
